'use strict';

var axios = require('axios');

var axiosDebug = require('debug')('axios');

var options = {
  request: function request(debug, config) {
    debug(config.method.toUpperCase() + ' ' + config.url);
  },
  response: function response(debug, _response) {
    debug(_response.status + ' ' + _response.statusText, '(' + _response.config.method.toUpperCase() + ' ' + _response.config.url + ')');
  },
  error: function error(debug, _error) {
    if (_error.config) {
      debug(_error.name + ': ' + _error.message, '(' + _error.config.method.toUpperCase() + ' ' + _error.config.url + ')');
    } else {
      debug(_error.name + ': ' + _error.message);
    }
  }
};

function addLogger(instance, debug) {
  if (debug === undefined) debug = axiosDebug;
  instance.interceptors.request.use(function (config) {
    options.request(debug, config);
    return config;
  });
  instance.interceptors.response.use(function (response) {
    options.response(debug, response);
    return response;
  }, function (error) {
    options.error(debug, error);
    throw error;
  });
}

addLogger(axios);

axios.create = function (originalCreate) {
  return function create() {
    var instance = originalCreate.apply(this, arguments);
    addLogger(instance);
    return instance;
  };
}(axios.create);

exports = module.exports = function (userOptions) {
  for (var key in options) {
    if (key in userOptions) {
      options[key] = userOptions[key];
    }
  }
};

exports.addLogger = addLogger;